.form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 20px;

}

.form .form-title {
  font-size: 20px;
  color: white;
  margin:-50px -10px 20px -10px;
}

#login-form {
  border-radius: 10px;
  border: 2px solid black;
  border-top-width: 35px;
  -webkit-animation: homepage-anim 1s;
          animation: homepage-anim 1s;
  background-color: white;

}

@-webkit-keyframes homepage-anim {
  from {opacity: 0;
  }
  to {opacity: 1;
  }
}

@keyframes homepage-anim {
  from {opacity: 0;
  }
  to {opacity: 1;
  }
}



