a {
  text-decoration: none;
}
@media (min-width: 801px) {
  .main-menu {
    display: grid;
    grid-template-columns: 20% auto 20%;
    grid-template-rows: 10% auto 10%;
    /* width : 95vw;
    height : 100vh; */
  }

  .main-menu .choices {
    grid-row-start: 2;
    grid-row-end: span 1;
    grid-column-start: 2;
    grid-column-end: span 1;
    border: 2px #3399ff solid;
    margin: auto;
    width: 50%;
    padding: 40px;
  }

  .hidden {
    visibility: hidden;
    position: absolute;
  }
  .signout-btn {
    -ms-flex-line-pack: center;
        align-content: center;
    padding: 8px;
    border: 1px #002e77 solid;
    -webkit-box-shadow: 0px 6px 0px #2b638f;
            box-shadow: 0px 6px 0px #2b638f;
    margin: auto;
    /* border-bottom-color: #002e77; */
    border-radius: 8px;
    background-color: white;
    /* border-bottom-width: 8px; */
  }
}

@media (max-width: 800px) {
  .main-menu {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 20% auto 10%;
    /* width : 95vw;
    height : 100vh; */
  }

  .main-menu .choices {
    grid-row-start: 2;
    grid-row-end: span 1;
    grid-column-start: 1;
    grid-column-end: span 1;
    border: 2px #3399ff solid;
    margin: auto;
    width: 50%;
    padding: 40px;
  }

  .hidden {
    visibility: hidden;
    position: absolute;
  }
  .signout-btn {
    -ms-flex-line-pack: center;
        align-content: center;
    grid-row-start: 1;
    grid-row-end: 2;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    width: 30%;
    padding: 8px;
    border: 1px #002e77 solid;
    -webkit-box-shadow: 0px 6px 0px #2b638f;
            box-shadow: 0px 6px 0px #2b638f;
    /* border-bottom-color: #002e77; */
    border-radius: 8px;
    background-color: white;
    /* border-bottom-width: 8px; */
  }
}



